<template>
  <el-card class="box-card">
    <el-button type="primary" @click="$router.push('/admin/main/employee/add-pilot')">Add Pilot</el-button>
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="id">
        <el-input placeholder="id" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="licenseid">
        <el-input placeholder="licenseid" v-model="param.licenseid"></el-input>
      </el-form-item>
      <el-form-item label="experience">
        <el-input placeholder="experience" v-model="param.experience"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="licenseid"
          label="licenseid"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          prop="experience"
          label="experience"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="operation"
      >
        <template v-slot="scope">
          <el-button type="danger" size="small" @click="handleDelete(scope.row.username)">Delete</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        licenseid: '',
        experience: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/pilots/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      if (this.param.licenseid && !/^\d{6}$/.test(this.param.licenseid)) {
        this.$message({
          message: 'It needs to be 6 digits',
          type: 'warning'
        });
        return;
      }
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    },
    handleDelete(username){
      this.$confirm('Are you sure to delete this pilots?', 'Warning', {
        confirmButtonText: 'OK',
        cancelButtonText: 'Cancel',
        type: 'warning'
      }).then(() => {
        this.$axios.get('/pilots/remove/' + username).then(() => {
          this.$message({
            type: 'success',
            message: 'Delete successfully!'
          });
          this.getTableList();
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: 'Delete canceled'
        });
      });
    }
  },
}
</script>

<style scoped lang="scss">
.demo-form-inline, .el-pagination {
  margin-top: 10px;
}
</style>
